import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { ProjectsByProjectManagerComponent } from '@app/ffufm-feedback-dashboard-features/projects-by-project-manager/projects-by-project-manager.component';

const projectsByProjectManagerRoutes: Routes = [
  {
    path: '',
    component: ProjectsByProjectManagerComponent
  }
];

@NgModule({
  declarations: [],
  imports: [
    RouterModule.forChild(projectsByProjectManagerRoutes)
  ],
  exports: [RouterModule]
})

export class ProjectsByProjectManagerRouting {}


